<template lang="html">
	<div class="container" ref='wyContainer'>

		<div id='msg' v-show="true">{{msg}}</div>
		<div id="logo">

		</div>
		<div id='content'>
			<ul id="ui_container">
				<li class="line_li" v-show="isShow2">
					<div class="title_div_li">
						&nbsp;&nbsp;&nbsp;&nbsp;
					</div>
					<div class="li_content_div_right">
						<input class="input-box" type="text" id="mobile" v-model="mobile" @focus="removeErrorClass">
					</div>
				</li>
				<li class="line_li" v-show="isShow2">
					<div class="title_div_li">
						
					</div>
					<div class="li_content_div_right">
						<input class="input-box" type="text" id="txtVerfiyCode" v-model="verifyCode" @focus="removeErrorClass">
						<span id='btn' class="add-btn click-send" @click="getVerifyCode" v-show="isShow"></span>
						<!--<button id="btn" class="add-btn" @click="getVerifyCode" v-show="isShow"></button>-->
					</div>
				</li>
				<li class="line_li" v-show="isShow1">
					<div class="title_div_li">
						
					</div>
					<div class="li_content_div_right">
						<input class="input-box" type="text" v-model="name" v-verify='name' />
						<label v-verified="verifyError.name"></label>
					</div>

				</li>
				<li class="line_li" v-show="isShow1">
					<div class="title_div_li">
						
					</div>
					<div class="li_content_div_right">
						<input type="radio" v-model="sex" value="" class="sex_choice" /> <span class="sex"></span>
						<input type="radio" v-model="sex" value="" class="sex_choice" /><span class="sex"></span>
						<label v-verified="verifyError.sex"></label>
					</div>

				</li>
				<li class="line_li" v-show="isShow1">
					<div class="title_div_li">
						
					</div>
					<div class="li_content_div_right">
						<input class="date_plugin" type="date" v-model="birthday" v-verify='birthday' />
						<label v-verified="verifyError.birthday"></label>
					</div>

				</li>
				<li class="line_li" v-show="isShow1">
					<div class="title_div_li">
						
					</div>
					<div class="li_content_div_right">
						<input class="input-box" type="text" v-model="cardNumber" v-verify='cardNumber' />
						<label v-verified="verifyError.cardNumber"></label>
					</div>
				</li>

				<li class="line_li" v-show="isShow2">
					<button class="add-btn register-btn" @click="register"></button>
				</li>

				<li class="line_li" v-show='isShow1'>
					<div style="text-align:center;padding-top:20px;">
						<button class="add-btn register-btn" @click="submitInfo"></button>
					</div>
				</li>

			</ul>
		</div>
		<div id='p_bg'></div>
	</div>
</template>

<script>
	import axios from 'axios'
	import Vue from "vue";
	import verify from "vue-verify-plugin";
	import domainUtils from '../utils/domain'

	Vue.use(verify, {
		blur: true
	});

	export default {
		data() {
			return {
				isShow1: false,
				isShow: true,
				isShow2: true,

				msg: "",
				name: '',
				sex: '',
				birthday: '',
				cardNumber: '',
				mobile: '',
				verifyCode: '',
				wxOpenId: '',
				retComponent: ''
			}
		},
		verify: {
			name: [ //
				"required",
				{
					test: function(val) {
						if(val.length < 2) {
							return false;
						}
						return true;
					},
					message: "2"
				}
			],
			//			mobile: "required",
			cardNumber: [
				"required", {
					minLength: 18,
					maxLength: 20,
					message: ""
				}
			],
			birthday: "required",
			sex: "required"

		},

		methods: {
			getVerifyCode: function() {
				var url = domainUtils.domain + "/getVerifyCode"
				if(this._check1()) {
					this.$post(url, {
						mobile: this.mobile
					}).then((data) => {
						if(data.status == "success") {
							this.msg = '';
							this.isShow = false;
							//							this.isShow1 = true;
						} else {
							if(data.remark == 'hasRegisted') {
								alert('')
//								this.msg = ''
							} else if(data.remark == 'mobileLimit') {
								alert('')
//								this.msg = ''
							} else if(data.remark == 'sendError') {
								alert("")
//								this.msg = ''
							} else if(data.remark == 'error') {
								alert("")						//this.msg = "authcode sms no authorization"
//								this.msg = ""
							} else if(data.remark == 'mobileError') {
								alert("")
//								this.msg = ""
							} else if(data.remark == "insertError") {
								alert("500 ")
//								this.msg = "500 "
							}
						}
					})
				} else {
					alert("")
				}
			},
			register: function() {
				var wxOpenId = window.localStorage.getItem('wxOpenId')
				
				var url = domainUtils.domain + "/register";
				if(this._check2()) {
					this.$post(url, {
						telephone: this.mobile,
						verifyCode: this.verifyCode,
						wxOpenId: wxOpenId
					}).then((data) => {
						if(data.status == "success") {
							alert("")
//							this.msg = ""
							this.isShow2 = false;
							this.isShow1 = true;
							//								msg = "registerSuccess";
						} else {
							if(data.remark == "codeError") {
								alert("")
//								this.msg = ""
							} else if(data.remark == "500") {
								alert("500 ")
//								this.msg = "500 "
							} else if(data.remark == 'overtime') {
								alert("")
//								this.msg = ""
							}else if(data.remark == "codeNotSend"){
								alert("")
							}
							
//							this.$router.push({
//								name: this.retComponent,
//								params: {
//									msg: data.remark
//								}
//							})

						}
					})
				} else {
					alert('4')
				}

			},
			submitInfo: function() {
				var wxOpenId = window.localStorage.getItem('wxOpenId')
				var url = domainUtils.domain + "/addCustomerInfo";
				if(this.$verify.check()) {
					this.$post(url, {
						name: this.name,
						birthday: this.birthday,
						cardNumber: this.cardNumber,
						sex: this.sex,
						wxOpenId: wxOpenId
					}).then((data) => {
						if(data.status == "success") {
							this.$router.push({
								name: this.retComponent,
								params: {
									msg: "infoAddSuccess"
								}
							})
						} else {
							if(data.remark == 'paramsNotComplete') {
								this.msg = ""
							} else if(data.remark == '500') {
								this.msg = "500 "
							}

							this.$router.push({
								name: this.retComponent,
								params: {
									msg: this.msg
								}
							})
						}
					})
				} else {

				}

			},
			_check1: function() {
				let container = this.$refs.wyContainer
				let mobile = container.querySelector('#mobile')
				if(this.mobile === '' || this.mobile.length !== 11) {
					mobile.classList.add('error-border')
				} else {
					return true
				}
				return false
			},
			_check2: function() {
				let container = this.$refs.wyContainer
				let txtVerfiyCode = container.querySelector('#txtVerfiyCode')
				if(this.verifyCode === '' || this.verifyCode.length !== 4) {
					txtVerfiyCode.classList.add('error-border')
				} else {
					return true
				}
				return false
			},
			removeErrorClass: function(e) {
				e.target.classList.remove('error-border')
			},
			getRetName() {
				var retName = this.$route.params.retComponent;
				if(retName == 0) {
					this.retComponent = "wyArtMuseumAppointmentCenter";

				} 
				if(retName == 1) {
					this.retComponent = "wyCellStorageAppointmentCenter";
				}
			}
		},
		computed: { //
			verifyError() {
				return this.$verify.$errors;
			}
		},
		created() {
			this.getRetName()
		},
		components: {}
	}
</script>

<style lang="scss">
	@import "../assets/scss/common.scss";
	@import "../assets/scss/reset.css";
	@import "../assets/scss/register.scss";
</style>


